import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid,
  Chip,
  IconButton
} from '@mui/material';
import {
  Save as SaveIcon,
  Cancel as CancelIcon,
  Add as AddIcon,
  PersonRemove as PersonRemoveIcon
} from '@mui/icons-material';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import projectsService from '../../services/projects';
import { databases } from '../../appwrite';
import { Query } from 'appwrite';

const ProjectForm = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [availableUsers, setAvailableUsers] = useState([]);
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    status: 'Not Started',
    colorTag: '#1976d2',
    accessGroup: []
  });
  const [addMemberEmail, setAddMemberEmail] = useState('');

  useEffect(() => {
    if (id && id !== 'new') {
      fetchProject();
    }
    loadUsers();
  }, [id]);

  const loadUsers = async () => {
    try {
      const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';
      const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
      const res = await databases.listDocuments(DATABASE_ID, PROFILES_COLLECTION_ID);
      setAvailableUsers(res.documents || []);
    } catch (err) {
      console.error('Failed to load users:', err);
    }
  };

  const fetchProject = async () => {
    try {
      const project = await projectsService.getProject(id);
      setFormData({
        name: project.name || '',
        description: project.description || '',
        status: project.status || 'Not Started',
        colorTag: project.colorTag || '#1976d2',
        accessGroup: project.accessGroup || []
      });
    } catch (err) {
      setError('Failed to load project');
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleAddMember = () => {
    if (!addMemberEmail.trim()) return;
    
    const userProfile = availableUsers.find(u => 
      u.email?.toLowerCase() === addMemberEmail.toLowerCase()
    );
    
    if (!userProfile) {
      setError('User not found with that email');
      setTimeout(() => setError(''), 3000);
      return;
    }
    
    if (formData.accessGroup.includes(userProfile.userId)) {
      setError('User is already in the access group');
      setTimeout(() => setError(''), 3000);
      return;
    }
    
    setFormData(prev => ({
      ...prev,
      accessGroup: [...prev.accessGroup, userProfile.userId]
    }));
    setAddMemberEmail('');
  };

  const handleRemoveMember = (userId) => {
    setFormData(prev => ({
      ...prev,
      accessGroup: prev.accessGroup.filter(id => id !== userId)
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.name.trim()) {
      setError('Project name is required');
      return;
    }

    setLoading(true);
    setError('');

    try {
      if (id && id !== 'new') {
        await projectsService.updateProject(id, formData);
        navigate(`/projects/${id}`);
      } else {
        const project = await projectsService.createProject(user.$id, formData);
        navigate(`/projects/${project.$id}`);
      }
    } catch (err) {
      setError(err?.message || 'Failed to save project');
    } finally {
      setLoading(false);
    }
  };

  const colorOptions = [
    '#1976d2', '#d32f2f', '#388e3c', '#f57c00', '#7b1fa2',
    '#0288d1', '#c2185b', '#00796b', '#fbc02d', '#5d4037'
  ];

  const getMemberName = (userId) => {
    const userProfile = availableUsers.find(u => u.userId === userId);
    return userProfile?.name || userProfile?.email || userId;
  };

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Typography variant="h4" sx={{ mb: 4, fontWeight: 'bold', color: 'primary.main' }}>
        {id && id !== 'new' ? 'Edit Project' : 'New Project'}
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError('')}>
          {error}
        </Alert>
      )}

      <form onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Project Name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
              sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
            />
          </Grid>

          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              multiline
              rows={4}
              sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
            />
          </Grid>

          <Grid item xs={12} sm={6}>
            <FormControl fullWidth>
              <InputLabel>Status</InputLabel>
              <Select
                name="status"
                value={formData.status}
                onChange={handleChange}
                label="Status"
              >
                <MenuItem value="Not Started">Not Started</MenuItem>
                <MenuItem value="Active">Active</MenuItem>
                <MenuItem value="On Hold">On Hold</MenuItem>
                <MenuItem value="Completed">Completed</MenuItem>
                <MenuItem value="Cancelled">Cancelled</MenuItem>
              </Select>
            </FormControl>
          </Grid>

          <Grid item xs={12} sm={6}>
            <FormControl fullWidth>
              <InputLabel>Color Tag</InputLabel>
              <Select
                name="colorTag"
                value={formData.colorTag}
                onChange={handleChange}
                label="Color Tag"
              >
                {colorOptions.map(color => (
                  <MenuItem key={color} value={color}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Box
                        sx={{
                          width: 20,
                          height: 20,
                          borderRadius: '50%',
                          backgroundColor: color,
                          border: '1px solid #ccc'
                        }}
                      />
                      {color}
                    </Box>
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>

          <Grid item xs={12}>
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
              Project Access Group
            </Typography>
            <Box sx={{ display: 'flex', gap: 1, mb: 1 }}>
              <TextField
                fullWidth
                size="small"
                placeholder="Enter email address"
                value={addMemberEmail}
                onChange={(e) => setAddMemberEmail(e.target.value)}
                onKeyPress={(e) => {
                  if (e.key === 'Enter') {
                    e.preventDefault();
                    handleAddMember();
                  }
                }}
              />
              <Button
                variant="outlined"
                startIcon={<AddIcon />}
                onClick={handleAddMember}
                size="small"
              >
                Add
              </Button>
            </Box>
            {formData.accessGroup.length > 0 && (
              <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mt: 1 }}>
                {formData.accessGroup.map(userId => (
                  <Chip
                    key={userId}
                    label={getMemberName(userId)}
                    size="small"
                    onDelete={() => handleRemoveMember(userId)}
                    deleteIcon={<PersonRemoveIcon />}
                  />
                ))}
              </Box>
            )}
          </Grid>

          <Grid item xs={12}>
            <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
              <Button
                variant="outlined"
                startIcon={<CancelIcon />}
                onClick={() => navigate(-1)}
                sx={{ borderRadius: 1, textTransform: 'none' }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="contained"
                startIcon={<SaveIcon />}
                disabled={loading || !formData.name.trim()}
                sx={{ borderRadius: 1, textTransform: 'none' }}
              >
                {loading ? 'Saving...' : 'Save Project'}
              </Button>
            </Box>
          </Grid>
        </Grid>
      </form>
    </Container>
  );
};

export default ProjectForm;

